
Template.body.addContent((function() {
  var view = this;
  return [ HTML.Raw("<h1>Welcome to the Work Tracker</h1>\n\n  "), Spacebars.include(view.lookupTemplate("hello")), "\n  ", Spacebars.include(view.lookupTemplate("info")) ];
}));
Meteor.startup(Template.body.renderToDocument);

Template.__checkName("hello");
Template["hello"] = new Template("Template.hello", (function() {
  var view = this;
  return "";
}));

Template.__checkName("info");
Template["info"] = new Template("Template.info", (function() {
  var view = this;
  return "";
}));
